body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.App {
  text-align: center;
}

.App-Header {
  background-color: black;
  color: white;
  height: 60px;
  padding: 10px;
}

.game {
  position: relative;
  width: auto;
  min-height: 60%;
  margin: 10px;
  padding: 20px;
  background-color: tomato;
  color: antiquewhite;
}

.random-label {
  font-size: 2.1rem;
  margin-left: 12px;
}

.switch input {
  display: none;
}

.switch {
  position: relative;
  display: inline-block;
  width: 60px;
  height: 34px;
  margin-bottom: -8px;
}

.slider {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: #ccc;
  cursor: pointer;
  transition: .4s;
}

.slider::before {
  position: absolute;
  content: "";
  left: 4px;
  bottom: 4px;
  width: 26px;
  height: 26px;
  background-color: white;
  transition: .4s;
}

input:checked+.slider {
  background-color: steelblue;
}

input:focus+.slider {
  box-shadow: 0 0 10px steelblue;
}

input:checked+.slider::before {
  transform: translate(26px);
}

.slider.round {
  border-radius: 34px;
}

.slider.round::before {
  border-radius: 50%;
}

.fields {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: stretch;
  flex: 1;
  margin: 20px;
}

.field-block {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  min-height: 160px;
  background-color: rgb(215, 22, 122);
  font-size: 12rem;
  color: white;
  font-weight: 300;
  padding: 2%;
}

.buttons {
  display: flex;
  justify-content: center;
  flex: 1;
}

.button {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  background-color: green;
  color: white;
  font-weight: 300;
  height: 45px;
  font-size: 1.6rem;
  margin-left: 20px;
  margin-right: 20px;
  padding-left: 40px;
  padding-right: 40px;
  cursor: pointer;
}

.prev {
  background-color: rgb(75, 218, 8);
}

.sound {
  padding-left: 15px;
  padding-right: 15px;
}

.next {
  background-color: rgb(0, 156, 184)
}

.left-field {
  width: 45%;
  height: 100%;
}

.right-field {
  width: 45%;
  height: 100%;
}

.placeholder-span {
  font-size: 1.1rem;
}

.hide {
  display: none !important;
}

.letter-image {
  width: 200px;
  height: 200px;
}

.word {
  font-size: 4.2rem;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  font-weight: 300;
  color: white;
}